import io from 'socket.io-client'
import { httpService } from './http.service'

export const SOCKET_EMIT_USER_WATCH = 'user watch'
export const SOCKET_EVENT_USER_UPDATED = 'user updated'
export const SOCKET_EVENT_BOARD_UPDATED = 'board-updated'
// export const SOCKET_EVENT_ = 'review-about-you'


const baseUrl = (process.env.NODE_ENV === 'production') ? '' : '//localhost:3030'
export const socketService = createSocketService()
// export const socketService = createDummySocketService()

window.socketService = socketService

// var socketIsReady = false
socketService.setup()


function createSocketService() {
    var socket = null
    const socketService = {
        async setup() {
            // YaronB: Need to send a dummy ajax request as to setup the socket-session correctly
            // await httpService.get('setup-session')
            // socket = io(baseUrl, { reconnection: false })
            socket = io(baseUrl)
            // socketIsReady = true
        },
        on(eventName, cb) {
            // if (!socket) await socketService.setup()
            socket.on(eventName, cb)
        },
        off(eventName, cb = null) {
            // if (!socket) await socketService.setup()
            if (!socket) return
            if (!cb) socket.removeAllListeners(eventName)
            else socket.off(eventName, cb)
        },
        emit(eventName, data) {
            if (!data) return
            // if (!socket) await socketService.setup()
            socket.emit(eventName, data)
        },
        terminate() {
            socket = null
            // socketIsReady = false
        }
    }
    return socketService
}

// eslint-disable-next-line
function createDummySocketService() {
    var listenersMap = {}
    const socketService = {
        listenersMap,
        setup() {
            listenersMap = {}
        },
        terminate() {
            this.setup()
        },
        on(eventName, cb) {
            listenersMap[eventName] = [...(listenersMap[eventName]) || [], cb]
        },
        off(eventName, cb) {
            if (!listenersMap[eventName]) return
            if (!cb) delete listenersMap[eventName]
            else listenersMap[eventName] = listenersMap[eventName].filter(l => l !== cb)
        },
        emit(eventName, data) {
            if (!listenersMap?.[eventName]) return
            listenersMap[eventName].forEach(listener => {
                listener(data)
            })
        },
        debugMsg() {
            this.emit('chat addMsg', { from: 'Someone', txt: 'Aha it worked!' })
        },
    }
    return socketService
}


